export default {
	props: {
		value: {
			type: [Number, String],
			default: 1
		},
		min: {
			type: Number,
			default: 1
		},
		max: {
			type: Number,
			default: 9999
		},
		step: {
			type: Number,
			default: 1
		},
		disabled: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			inputValue: 1
		}
	},
	watch: {
		value(val) {
			this.inputValue = +val
		},
		inputValue(newVal, oldVal) {
			if (+newVal !== +oldVal) {
				this.$emit('change', newVal)
			}
		}
	},
	created() {
		this.inputValue = +this.value
	},
	methods: {
		_calcValue(type) {
			if (this.disabled) {
				return
			}
			let value = parseInt(this.inputValue )
			let step = parseInt(this.step) 
			if (type === 'minus') {
				value -= step
			} else if (type === 'plus') {
				value += step
			}
			if (value < this.min || value > this.max) {
				return
			}
			this.inputValue =  parseInt(value)
		},
		_onBlur(event) {
			let value =parseInt(event.detail.value)
			if (!value) {
				this.inputValue = 1
				return
			}
			value = +value
			if (value > this.max) {
				value = this.max
			} else if (value < this.min) {
				value = this.min
			}
			this.inputValue = parseInt(value) 
		}
	}
}
